import React, { useState } from "react";
import styled from "styled-components";
import { InfoForm } from "./info";
import { motion } from "framer-motion";
import { AccountContext } from "./accountContext";
import { NextForm } from "./next";

const BoxContainer = styled.div`
  width: 400px;
  min-height: 550px;
  display: flex;
  flex-direction: column;
  border-radius: 19px;
  background-color: #fff;
  box-shadow: 0 0 2px rgba(15, 15, 15, 0.28);
  position: relative;
  overflow: hidden;
`;

const TopContainer = styled.div`
  width: 100%;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 1.8em;
  padding-bottom: 5em;
`;

const BackDrop = styled(motion.div)`
  width: 160%;
  height: 550px;
  position: absolute;
  display: flex;
  flex-direction: column;
  border-radius: 50%;
  transform: rotate(60deg);
  top: -290px;
  left: -70px;
  background: rgb(8, 137, 170);
  background: linear-gradient(
    90deg,
    rgba(8, 112, 170, 1) 0%,
    rgba(1, 203, 255, 1) 73%,
    rgba(102, 252, 241, 1) 93%
  );
`;

const HeaderContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const HeaderText = styled.h2`
  font-size: 30px;
  font-weight: 600;
  line-height: 1.25;
  color: #000;
  z-index: 10;
  margin: 0;
`;

const SmallText = styled.h5`
  color: #042202;
  font-weight: 11px;
  font-size: 11px;
  z-index: 10;
  margin: 0;
  margin-top: 7px;
`;

const InnerContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0 1.8em;
`;

const backdropVariants = {
  expanded: {
    width: "233%",
    height: "190%",
    borderRadius: "20%",
    transform: "rotate(60deg)"
  },
  collapsed: {
    width: "130%",
    height: "90%",
    borderRadius: "50%",
    transform: "rotate(60deg)"
  }
};

const expandingTransition = {
  type: "spring",
  duration: 2.3,
  stiffness: 30
};

export function AccountBox(props) {
  const [isExpanded, setExpanded] = useState(false);
  const [active, setActive] = useState("next");

  const playExpandingAnimation = () => {
    setExpanded(true);
    setTimeout(() => {
      setExpanded(false);
    }, expandingTransition.duration * 1000 - 1500);
  };

  const switchToNext = () => {
    playExpandingAnimation();
    setTimeout(() => {
      setActive("next");
    }, 400);
  };

  const switchToInfo = () => {
    playExpandingAnimation();
    setTimeout(() => {
      setActive("info");
    }, 400);
  };

  const contextValue = { switchToNext, switchToInfo };

  return (
    <AccountContext.Provider value={contextValue}>
      <BoxContainer>
        <TopContainer>
          <BackDrop
            initial={false}
            animate={isExpanded ? "expanded" : "collapsed"}
            variants={backdropVariants}
            transition={expandingTransition}
          />
          {active === "info" && (
            <HeaderContainer>
              <HeaderText>Submit Entries</HeaderText>
              <SmallText>Please fill the details to proceed</SmallText>
            </HeaderContainer>
          )}
          {active === "next" && (
            <HeaderContainer>
              <HeaderText>About to Finish</HeaderText>
              <SmallText>Please select correct options!</SmallText>
            </HeaderContainer>
          )}
        </TopContainer>
        <InnerContainer>
          {active === "info" && <InfoForm />}
          {active === "next" && <NextForm />}
        </InnerContainer>
      </BoxContainer>
    </AccountContext.Provider>
  );
}
