import React, { useContext } from "react";
import {
  BoxContainer,
  FormContainer,
  Input,
  SubmitButton,
  SmallText
} from "./common";
import { Form } from "react-bootstrap";
import { Marginer } from "../marginer";
import { AccountContext } from "./accountContext";
// import { RadioGroup, RadioButton } from "react-radio-buttons";

export function NextForm(props) {
  const { switchToInfo } = useContext(AccountContext);

  return (
    <BoxContainer>
      <FormContainer>
        <SmallText>Select Correct Type</SmallText>
        <fieldset>
          <Form.Check
            type="radio"
            label="Drawings"
            name="formHorizontalRadios"
            id="formHorizontalRadios1"
          />
          <Form.Check
            type="radio"
            label="Photography"
            name="formHorizontalRadios"
            id="formHorizontalRadios2"
          />
        </fieldset>
        <SmallText>Submit Your Art here</SmallText>
        <Input type="file" />
      </FormContainer>
      <Marginer direction="vertical" margin="1.6em" />
      <SubmitButton type="submit" onClick={switchToInfo}>
        Back
      </SubmitButton>{" "}
      <SubmitButton type="submit">Submit</SubmitButton>
      <Marginer direction="vertical" margin="1.6em" />
    </BoxContainer>
  );
}
