import React, { useContext } from "react";
import { InputGroup, FormControl } from "react-bootstrap";
import { BoldText, BoxContainer, FormContainer, SubmitButton } from "./common";
import { Marginer } from "../marginer";
import { AccountContext } from "./accountContext";

export function InfoForm(props) {
  const { switchToNext } = useContext(AccountContext);

  return (
    <BoxContainer>
      <BoldText>Student Details </BoldText>
      <FormContainer>
        <InputGroup className="mb-1">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Full Name</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Name"
            aria-label="Username"
            aria-describedby="basic-addon1"
          />
        </InputGroup>
        <InputGroup className="mb-1">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon2">@ </InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Email-Id"
            aria-label="Email"
            aria-describedby="basic-addon2"
          />
        </InputGroup>
        <InputGroup className="mb-1">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon3">Roll No. </InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Roll No"
            aria-label="rollno"
            aria-describedby="basic-addon3"
          />
        </InputGroup>
        <InputGroup className="mb-1">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon4">Year</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Year of Study"
            aria-label="std"
            aria-describedby="basic-addon4"
          />
        </InputGroup>
        <InputGroup className="sm">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon5">College</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder=""
            aria-label="clg"
            aria-describedby="basic-addon5"
          />
        </InputGroup>
      </FormContainer>
      <Marginer direction="vertical" margin={10} />
      <Marginer direction="vertical" margin="1.6em" />
      <SubmitButton type="submit" onClick={switchToNext}>
        Next
      </SubmitButton>
      <Marginer direction="vertical" margin="1em" />
    </BoxContainer>
  );
}
